/*
Name: 			Theme Base
Written by: 	Okler Themes - (http://www.okler.net)
Theme Version: 	1.5.5
*/
html,
body {
  background: #ecedf0;
  width: 100%;
}

html {
  font-size: 10px;
  overflow-x: hidden !important;
  overflow-y: scroll !important;
}

body {
  color: #777;
  font-family: "Open Sans", Arial, sans-serif;
  line-height: 22px;
  margin: 0;
  font-size: 13px;
}

a {
  color: #ccc;
}

a:hover,
a:focus {
  color: #d9d9d9;
}

a:active {
  color: #bfbfbf;
}

/* Layout Base - Main Wrapper	*/
.body {
  min-height: 100vh;
  width: 100%;
}

/* Layout Base - Header */
.header {
  height: 60px;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}

/* Layout Base - Inner Wrapper */
.inner-wrapper {
  display: table;
  min-height: 100vh;
  padding-top: 60px;
  table-layout: fixed;
  overflow: hidden;
  width: 100%;
}

/* Layout Base - Content Body */
.content-body {
  display: table-cell;
  padding: 40px;
  position: relative;
  vertical-align: top;
}

.content-body > .row + .row {
  padding-top: 10px;
}

/* Layout Base - Page Header */
.page-header {
  background: #171717;
  border-bottom: none;
  border-left: 1px solid #3a3a3a;
  box-shadow: 1px 3px 0 1px #cccccc;
  height: 50px;
  margin: -40px -40px 40px -40px;
  padding: 0;
}

/* Layout Base - Sidebar Left */
.sidebar-left {
  color: #777;
  display: table-cell;
  position: relative;
  vertical-align: top;
  width: 300px;
  z-index: 1010;
}

/* Layout Base - Sidebar Left Opened ( Larger than mobile ) */
@media only screen and (min-width: 768px) {
  html.sidebar-left-collapsed .sidebar-left {
    width: 73px;
  }
}
/* Layout Base - Sidebar Right */
.sidebar-right {
  background: #171717;
  bottom: 0;
  margin-right: -300px;
  min-height: 100vh;
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
}

/* Layout Base - Sidebar Right Opened ( Larger than mobile ) */
@media only screen and (min-width: 768px) {
  html.sidebar-right-opened .header {
    margin-left: -300px;
    margin-right: 300px;
  }

  html.sidebar-right-opened .inner-wrapper {
    margin-left: -300px;
  }

  html.sidebar-right-opened .sidebar-right {
    margin-right: 0;
  }
}
/* Layout Base - Flexbox supported */
@media only screen and (min-width: 768px) {
  html.flexbox .inner-wrapper,
  html.flexboxlegacy .inner-wrapper {
    display: -webkit-box;
    display: -moz-box;
    display: box;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flexbox;
    display: flex;
  }

  html.flexbox .sidebar-left,
  html.flexbox .content-body,
  html.flexboxlegacy .sidebar-left,
  html.flexboxlegacy .content-body {
    display: block;
    -webkit-flex-shrink: 0;
    -moz-flex-shrink: 0;
    flex-shrink: 0;
    -ms-flex-negative: 0;
  }

  html.flexbox .content-body,
  html.flexboxlegacy .content-body {
    -webkit-box-flex: 2;
    -moz-box-flex: 2;
    box-flex: 2;
    -webkit-flex: 2;
    -moz-flex: 2;
    -ms-flex: 2;
    flex: 2;
  }
}
/* Layout Fixed */
@media only screen and (min-width: 768px) {
  /* Layout Fixed - Reseting Styles */
  html.fixed .inner-wrapper,
  html.fixed .sidebar-left,
  html.fixed .content-body {
    display: block;
  }

  /* Layout Fixed - Header */
  html.fixed .header {
    position: fixed;
    z-index: 1020;
  }

  /* Layout Fixed - Inner Wrapper */
  html.fixed .inner-wrapper {
    padding-top: 110px;
  }

  /* Layout Fixed - Content Body */
  html.fixed .content-body {
    margin-left: 300px;
  }

  html.fixed .content-body.has-toolbar {
    padding-top: 92px;
  }

  /* Layout Fixed - Page header */
  html.fixed .page-header {
    left: 300px;
    margin: 0;
    position: fixed;
    right: 0;
    top: 60px;
  }

  /* Layout Fixed - Sidebar Left */
  html.fixed .sidebar-left {
    bottom: 0;
    left: 0;
    padding-bottom: 50px;
    position: fixed;
    top: 60px;
  }

  html.fixed .sidebar-left .nano-content {
    padding-bottom: 50px;
  }

  /* Layout Fixed - Sidebar Left Collapsed */
  html.fixed.sidebar-left-collapsed .page-header {
    left: 73px;
  }

  html.fixed.sidebar-left-collapsed .content-body {
    margin-left: 73px;
  }

  /* Layout Fixed - Sidebar Right Opened */
  html.fixed.sidebar-right-opened .page-header {
    left: 0;
    margin-right: 300px;
  }

  html.fixed.sidebar-right-opened .sidebar-left {
    left: -300px;
  }

  /* Layout Fixed - Sidebar Left Collapsed & Sidebar Right Opened */
  html.fixed.sidebar-left-collapsed.sidebar-right-opened .page-header {
    left: -300px;
  }
}
/* Layout Boxed - small than min-width */
@media only screen and (max-width: 1199px) {
  html.boxed .header {
    border-color: #ccc;
  }
}
/* Layout Boxed - larger or equal min width */
@media only screen and (min-width: 1200px) {
  /* Layout Boxed - Body Tag */
  html.boxed body {
    background: url(./images/patterns/gray_jean.png) repeat;
    padding-bottom: 5px;
  }

  /* Layout Boxed - Main Wrapper */
  html.boxed .body {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 25px;
    background-color: transparent;
  }

  /* Layout Boxed - Header */
  html.boxed .header {
    border-top: 3px solid #ccc;
    height: 60px;
    position: absolute;
    top: 25px;
  }

  /* Layout Boxed - Inner Wrapper */
  html.boxed .inner-wrapper {
    padding-top: 60px;
    margin-bottom: 20px;
    border-radius: 5px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.15);
  }

  /* Layout Boxed - Content Body */
  html.boxed .content-body {
    background-color: #ecedf0;
  }

  /* Layout Boxed - Base */
  html.boxed .header {
    border-radius: 5px 5px 0 0;
  }

  html.boxed .sidebar-left {
    border-radius: 0 0 0 5px;
    position: relative;
    margin-bottom: 0;
  }

  html.boxed .content-body {
    border-radius: 0 0 5px 0;
  }

  html.boxed .sidebar-right {
    border-radius: 0 5px 5px 0;
  }

  /* Layout Boxed - Sidebar Right */
  html.boxed .sidebar-right {
    border-top: 3px solid #ccc;
    min-height: 0;
  }

  /* Layout Boxed - Sidebar Right Opened */
  html.boxed.sidebar-right-opened .body {
    overflow: hidden;
  }

  html.boxed.sidebar-right-opened .header {
    border-radius: 5px 0 0 0;
  }

  html.boxed.sidebar-right-opened .content-body {
    border-radius: 0 0 0 5px;
  }

  html.boxed.sidebar-right-opened .sidebar-right {
    bottom: 20px;
    position: absolute;
    top: 25px;
    border-radius: 0 5px 5px 5px;
  }
}

@media only screen and (max-width: 767px) {
  html,
  body {
    background: #ecedf0;
  }

  html.mobile-device .sidebar-left,
  html.mobile-device .sidebar-right {
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
  }

  body {
    min-height: 100vh;
  }

  .inner-wrapper,
  .sidebar-left,
  .content-body {
    display: block;
  }

  .body {
    min-height: 0;
    overflow: visible;
  }

  .header {
    background: none;
    border: none;
    height: auto;
    position: static;
  }

  .header .logo-container {
    height: 60px;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 99;
  }

  .header .header-right {
    background: #fff;
    float: none !important;
    height: 60px;
    margin-top: 60px;
    width: 100%;
  }

  .inner-wrapper {
    min-height: 0;
    padding-top: 0;
  }

  .content-body {
    padding: 0 15px 15px;
  }

  .page-header {
    margin: 0 -15px 20px;
  }

  .sidebar-left {
    bottom: 0;
    left: -100%;
    min-height: 0;
    min-width: 100%;
    min-width: 100vw;
    padding-top: 60px;
    padding-bottom: 50px;
    position: fixed;
    overflow: hidden;
    top: 0;
    z-index: 98 !important;
  }

  .sidebar-right {
    bottom: 0;
    left: auto;
    right: -100%;
    min-height: 0;
    margin-right: 0;
    min-width: 100%;
    min-width: 100vw;
    top: 0;
    z-index: 100;
  }

  html.csstransforms .sidebar-left,
  html.csstransforms .sidebar-right {
    /* performs better but native android browser
		has problems with translate and percentage
		@include transition-property(transform);
		*/
    -webkit-transition-property: margin;
    -moz-transition-property: margin;
    transition-property: margin;
    -webkit-transition-duration: 0.25s;
    -moz-transition-duration: 0.25s;
    transition-duration: 0.25s;
    -webkit-transition-timing-function: ease-out;
    -moz-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
    -webkit-transition-delay: 0;
    -moz-transition-delay: 0;
    transition-delay: 0;
  }

  html.csstransforms .sidebar-left {
    /* performs better but native android browser
		has problems with translate and percentage
		@include transform( translateX(0) );
		*/
    margin-left: -25px;
  }

  html.csstransforms .sidebar-right {
    /* performs better but native android browser
		has problems with translate and percentage
		@include transform( translateX(0) );
		*/
    margin-right: -25px;
  }

  /* If desktop is seeing mobile res, fix scrollbars */
  html.no-mobile-device body {
    min-height: 0;
  }

  html.no-mobile-device .body {
    min-height: 100vh;
    overflow: hidden;
  }

  html.no-mobile-device .inner-wrapper {
    overflow-y: auto;
  }

  html.no-mobile-device.sidebar-left-opened,
  html.no-mobile-device.sidebar-left-opened body,
  html.no-mobile-device.sidebar-right-opened,
  html.no-mobile-device.sidebar-right-opened body {
    overflow: hidden;
  }

  /* Layout Mobile - Sidebar Left Opened */
  html.sidebar-left-opened.no-csstransforms .sidebar-left {
    left: 0;
  }

  html.sidebar-left-opened.csstransforms .sidebar-left {
    /* performs better but native android browser
		has problems with translate and percentage
		@include transform( translateX(100%) );
		*/
    margin-left: 100%;
  }

  /* Layout Mobile - Sidebar Right Opened */
  html.sidebar-right-opened.no-csstransforms .sidebar-right {
    right: 0;
  }

  html.sidebar-right-opened.csstransforms .sidebar-right {
    /* performs better but native android browser
		has problems with translate and percentage
		@include transform( translateX(-100%) );
		*/
    margin-right: 100%;
  }

  /* Layout Mobile - Sidebar Left Collapsed & Sidebar Right Opened */
  html.sidebar-left-collapsed.sidebar-right-opened .sidebar-left {
    margin-left: -300px;
  }
}
