.toggle {
  margin: 10px 0 0;
  position: relative;
  clear: both;
}

.toggle > input {
  cursor: pointer;
  filter: alpha(opacity=0);
  height: 45px;
  margin: 0;
  opacity: 0;
  position: absolute;
  width: 100%;
  z-index: 2;
}

.toggle > label {
  -webkit-transition: all 0.15s ease-out;
  -moz-transition: all 0.15s ease-out;
  transition: all 0.15s ease-out;
  background: #f4f4f4;
  border-left: 3px solid #ccc;
  border-radius: 5px;
  color: #ccc;
  display: block;
  font-size: 1.1em;
  min-height: 20px;
  padding: 12px 20px 12px 10px;
  position: relative;
  cursor: pointer;
  font-weight: 400;
}

.toggle > label:-moz-selection {
  background: none;
}

.toggle > label i.fa-minus {
  display: none;
}

.toggle > label i.fa-plus {
  display: inline;
}

.toggle > label:selection {
  background: none;
}

.toggle > label:before {
  border: 6px solid transparent;
  border-left-color: inherit;
  content: "";
  margin-top: -6px;
  position: absolute;
  right: 4px;
  top: 50%;
}

.toggle > label:hover {
  background: #f5f5f5;
}

.toggle > label + p {
  display: block;
  overflow: hidden;
  padding-left: 30px;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 25px;
}

.toggle > label i {
  font-size: 0.7em;
  margin-right: 8px;
  position: relative;
  top: -1px;
}

.toggle > .toggle-content {
  display: none;
}

.toggle > .toggle-content > p {
  margin-bottom: 0;
  padding: 10px 0;
}

.toggle.active i.fa-minus {
  display: inline;
  color: #fff;
}

.toggle.active i.fa-plus {
  display: none;
}

.toggle.active > label {
  background: #ccc;
  border-color: #ccc;
  color: #fff;
}

.toggle.active > label:before {
  border: 6px solid transparent;
  border-top-color: #fff;
  margin-top: -3px;
  right: 10px;
}

.toggle.active > p {
  white-space: normal;
}

.toggle > p.preview-active {
  height: auto;
  white-space: normal;
}

/* dark */
html.dark .toggle > label {
  background: #282d36;
}

